html { box-sizing: border-box; }
*, *:before, *:after { box-sizing: inherit; }

body{
  margin: 0px;
  padding: 0px;
}

.section{
  padding: 20px 0px;
}

.section__special{
  padding: 150px 0px 0px;
}

.section__nav-special{
  padding: 50px 0px;
}

.container {
  margin: 0 auto;
  width: auto;
  padding-left: 20px;
  padding-right: 20px;
}

.row {
  margin-left: -20px;
  margin-right: -20px;
}

//This allows for any class with col- to take the spedfics inside the {}
[class^="col-"], [class*=" col-"]{
  float: left;
  width: 100%;
  padding: 20px;
}

.col-outline{
  border: 1px solid #0E1721;
}

